@import url('https://fonts.googleapis.com/css2?family=Georama:wght@300&display=swap');


body {
  margin: 0;
  padding: 0;
  font-family: 'Georama', sans-serif; }

.form-in-modal {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center; }

.block-center {
  margin: auto;
  width: max-content; }

.title {
  font-size: 1.5rem;
  padding-top: 1rem; }

form {
  color: black;
  padding: 20px;
  padding-top: 0; }


textarea {
  width: 100%;
  border-radius: 4px;
  border: 0.1rem solid #bdbdbd;
  padding: 20px;
  padding-top: 35px;
  box-sizing: border-box;
  font-size: 1.2rem;
  outline-color: #add3f0;

  &:after {
      content: ''; } }


.accordion {
  border: 0.1rem solid #bdbdbd;
  border-radius: 4px;
  position: relative; }

.title-accordion {

  padding: 10px;
  cursor: pointer;
  transition: all .5s ease-in-out;
  display: flex;
  justify-content: space-between;
  border-radius: 4px; }



svg {
    transition: all .5s ease-in-out;
    transform: rotate(90deg);
    color: grey; }


.error-field {
  color: #fc0f56;
  border: 0.1rem solid #fc0f56!important;
  background: #ffe8ec;
  border-radius: 4px;

  & ~ label {
    color: #fc0f56!important; } }

.error-text {
  display: block!important;
  color: red;
  padding: 0;
  font-size: 0.7rem;
  margin: 0.5rem 0; }

.d-none {
  display: none; }



.show-accordion {
  outline: 2px solid #add3f0; }

.accordion-body {
  position: absolute;
  z-index: 1;
  background: white;
  width: 100%; }

.accordion-item {
  padding: 10px;
  cursor: pointer;
  transition: all .5s ease-in-out;
  border: 0.1rem solid #bdbdbd;
  border-bottom: none;
  color: black;

  &:hover {
    background: #e8feff; }

  &:last-of-type {
    border-bottom: 0.1rem solid #bdbdbd; }

  &:first-of-type {
    border-top: none; } }


.required {
    &:after {
      content: ' *'; } }

.form-field {
  display: flex;
  flex-direction: column-reverse;

  input {
    border: none;
    padding: 1rem;
    padding: 0.5rem 1rem;
    padding-top: 1.5rem;
    font-size: 1.2rem;
    border: 0.1rem solid #bdbdbd;
    outline: none;
    border-radius: 4px; }


  label {
    padding-left: 1.5rem;
    color: #808283;
    transform: translateY(2.5rem);
    transform-origin: left top;
    cursor: text;
    transition: all 0.3s; }

  input:focus ~ label, input:not(:placeholder-shown) ~ label {
    //padding: 0
    transform: translateY(1.7rem) scale(0.8); } }

textarea:focus ~ label, textarea:not(:placeholder-shown) ~ label {
  transform: translateY(1.7rem) scale(0.8); }

.field-around {
  display: flex;
  justify-content: space-between;
  margin: 15px 0;
  align-items: center; }


.w100 {
  width: 100%; }


.result-send {
  text-align: center; }

button {
  background: #4480dc;
  padding: 10px 20px;
  margin: 0 auto;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.1rem; }

.checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px; }

.checkbox + label {
  position: relative;
  //padding: 0 0 0 60px
  cursor: pointer; }

.checkbox + label:before {
  content: '';
  position: absolute;
  top: 0px;
  right: 0;
  width: 45px;
  height: 20px;
  border-radius: 13px;
  background: #d1bbbd;
  box-shadow: inset 0 2px 3px rgba(0,0,0,.2);
  transition: .2s; }

.checkbox + label:after {
  content: '';
  position: absolute;
  top: 2px;
  right: 27px;
  width: 15px;
  height: 15px;
  border-radius: 10px;
  background: #decbce;
  box-shadow: 0 2px 5px rgba(0,0,0,.3);
  transition: .2s; }

.checkbox:checked + label:before {
  background: #00ab42; }

.checkbox:checked + label:after {
  right: 2px; }

.checkbox:focus + label:before {
  box-shadow: inset 0 2px 3px rgba(0,0,0,.2), 0 0 0 1px rgba(76, 87, 142, 0.7); }

.field-select {
  position: relative;
  width: 50%; }



